        @import '../../assets/styles/Defaults/Defaults.scss';
        .main {
            display: flex;
            width: 100%;
            height: 100%;
            background-color: $blue;
        }
        
        .pic_section {
            width: 50%;
            display: flex;
            justify-content: center;
            img {
                width: 40%;
                padding-top: 1rem;
                border-radius: 45px 45px 0px 0px;
            }
        }
        
        .txt_section {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            justify-content: center;
            width: 50%;
            padding-right: 9rem;
            ;
            h1,
            h3 {
                color: white;
            }
        }
        
        .input {
            display: flex;
            justify-content: flex-end;
            width: 100%;
            input {
                border: none;
                border-radius: 0px 15px 15px 0px;
                direction: rtl;
                padding-right: 1rem;
                width: 60%;
            }
             ::placeholder {
                color: $placeholder;
            }
            input:focus {
                outline: 0;
            }
            button {
                border: none;
                border-radius: 15px 0px 0px 15px;
                background-color: white;
                padding: 1rem;
                cursor: pointer;
                p {
                    background-color: $blue;
                    padding: 0.5rem 1rem;
                    color: white;
                    border-radius: 5px;
                    font-size: 18px;
                }
            }
            button:focus {
                outline: 0;
            }
        }
        
        .btn_section {
            display: grid;
            grid-template-columns: auto auto auto;
            margin-top: 2rem;
            align-items: center;
            justify-content: space-evenly;
            width: 100%;
        }
        
        .btn_item {
            justify-content: space-around;
        }
        
        .android,
        .bazzar,
        .sibapp {
            display: flex;
            background-color: $epiblue;
            border: none;
            align-items: center;
            cursor: pointer;
            color: white;
            padding: 0.6rem 1rem;
            border-radius: 10px;
        }
        
        .android:focus,
        .bazzar:focus,
        .sibapp:focus {
            outline: 0;
        }
        
        .android {
            p {
                padding-right: 0.5rem;
            }
        }
        
        .bazzar {
            p {
                padding-left: 0.5rem;
            }
        }
        
        .textbazar {
            padding-left: 0.5rem;
            width: 36%;
        }
        
        .logobazar {
            width: 18%;
        }
        
        .sibapp {
            section {
                padding-right: 0.5rem;
                p {
                    font-size: 10px;
                }
            }
            padding: 0.4rem 1rem;
        }
        
        .logosib {
            width: 20%;
        }
        
        .logotext {
            width: 80%;
        }
        
        @media(max-width:1150px) {
            .txt_section {
                padding-right: 6rem;
            }
        }
        
        @media(max-width:1054px) {
            .txt_section {
                padding-right: 4rem;
            }
        }
        
        @media(max-width:992px) {
            .txt_section {
                padding-right: 2rem;
            }
        }
        
        @media(max-width:926px) {
            .txt_section {
                padding-right: 0.5rem;
                width: 55%;
            }
        }
        
        @media(max-width:840px) {
            .txt_section {
                padding: 0;
                align-items: center;
                padding-top: 2rem;
                h3 {
                    direction: rtl;
                }
                width: 100%;
            }
            .input {
                justify-content: center;
                width: 100%;
            }
            .main {
                flex-direction: column-reverse;
                align-items: center;
                justify-content: center;
            }
            .btn_section {
                grid-template-columns: auto;
            }
            .btn_item {
                button {
                    width: 100%;
                    justify-content: center;
                    margin-top: 0.5rem;
                }
            }
            .pic_section img {
                width: 80%;
            }
        }
        
        @media(max-width:375px) {
            .txt_section {
                h3 {
                    font-size: $body1;
                }
            }
        }
        
        @media(max-width:280px) {
            .txt_section {
                h1 {
                    font-size: $head3;
                    direction: rtl;
                }
                h3 {
                    font-size: 13px;
                }
            }
        }
        
        .error {
            margin-top: 1rem;
            margin-right: 1rem;
            color: white;
            font-size: 18px;
            display: none;
        }
        
        .active_error {
            display: block;
        }